import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/',
      redirect:'/pages/home'
    },
    {
      path: '/pages/home',
      name: 'home',
      component: () => import('@/pages/home')
    },
    {
      path:'/pages/bannerdetail :id',
      name:'bannerdetail',
      component:() => import('@/pages/bannerdetail')
    },
    {
      path:'/pages/navdetail :navid',
      name:'navdetail',
      component:() => import('@/pages/navdetail')
    },
    {
      path:'/pages/detail :index :name',
      name:'detail',
      component:() => import('@/pages/detail')
    }
  ]
})



